<template>
  <div>
    <!-- 添加待簽 -->
    <a-card title="Fixtrue預新增" v-if="role=='admin'||role=='IE_1'">
      <a-list
          class="demo-loadmore-list"
          item-layout="horizontal"
          v-bind:data-source="data"
          size="small"
        >
          <a-list-item slot="renderItem" slot-scope="item"  v-if="item.listid===undefined">
            <a slot="actions" style="color:green" @click="sub(item)">OK</a>
            <a slot="actions" style="color:red"  @click="delhcy(item.id)">NG</a>
            <a-list-item-meta>
              <div slot="title">
                機種：{{ item.model }}
                <a-divider type="vertical" />
                階段：{{ item.build }}
                <a-divider type="vertical" />
                no：{{ item.sno }}
                <a-divider type="vertical" />
                站位名：{{ item.sname }}
                <a-divider type="vertical" />
                QPL：{{ item.qpl }}
                <a-divider type="vertical" />
                Process：{{ item.process }}
                <a-divider type="vertical" />
                部門：{{ item.dept }}
               </div>
            </a-list-item-meta>
          </a-list-item>
        </a-list>
    </a-card>
    <a-divider dashed />
    <!-- IE修改待簽 -->
    <a-card title="Fixtrue預修改" v-if="role=='admin'||role=='IE_1'">
      <div>
        <a-list
          class="demo-loadmore-list"
          item-layout="horizontal"
          v-bind:data-source="data"
          size="small"
        >
          <a-list-item slot="renderItem" slot-scope="item"  v-if="item.listid!==undefined&&item.status=='2'">
            <a slot="actions" style="color:green" @click="sub(item)">OK</a>
            <a slot="actions" style="color:red"  @click="delhcy(item.id)">NG</a>
            <a-list-item-meta>
              <div slot="title">
                {{item.listid}}
                機種：{{ item.model }}
                <a-divider type="vertical" />
                階段：{{ item.build }}
                <a-divider type="vertical" />
                no：{{ item.sno }}
                <a-divider type="vertical" />
                站位名：{{ item.sname }}
                <a-divider type="vertical" />
                QPL：{{ item.qpl }}
                <a-divider type="vertical" />
                Process：{{ item.process }}
                <a-divider type="vertical" />
                部門：{{ item.dept }}
               </div>
            </a-list-item-meta>
          </a-list-item>
        </a-list>
      </div>
    </a-card>
    <!-- 非IE修改待簽 -->
    <a-card title="Fixtrue預修改" v-if="role!=='admin'&&role!=='IE_1'">
      <div>
        <a-list
          class="demo-loadmore-list"
          item-layout="horizontal"
          v-bind:data-source="data"
          size="small"
        >
          <a-list-item slot="renderItem" slot-scope="item"  v-if="item.listid!==undefined&&role.indexOf(item.dept)!=-1&&item.status=='2'">
            <a slot="actions" style="color:green" @click="sub(item)">OK</a>
            <a slot="actions" style="color:red"  @click="delhcy(item.id)">NG</a>
            <a-list-item-meta>
              <div slot="title">
                {{item.listid}}
                機種：{{ item.model }}
                <a-divider type="vertical" />
                階段：{{ item.build }}
                <a-divider type="vertical" />
                no：{{ item.sno }}
                <a-divider type="vertical" />
                站位名：{{ item.sname }}
                <a-divider type="vertical" />
                QPL：{{ item.qpl }}
                <a-divider type="vertical" />
                Process：{{ item.process }}
                <a-divider type="vertical" />
                部門：{{ item.dept }}
               </div>
            </a-list-item-meta>
          </a-list-item>
        </a-list>
      </div>
    </a-card>
    <a-divider dashed />
    <!-- 刪除待簽 -->
    <a-card title="Fixtrue預刪除" v-if="role=='admin'||role=='IE_1'" >
      <div>
        <a-list
          class="demo-loadmore-list"
          item-layout="horizontal"
          v-bind:data-source="data"
          size="small"
        >
          <a-list-item slot="renderItem" slot-scope="item"  v-if="item.listid!==undefined&&item.status=='1'">
            <a slot="actions" style="color:green" @click="sub(item)">OK</a>
            <a slot="actions" style="color:red"  @click="delhcy(item.id)">NG</a>
            <a-list-item-meta>
              <div slot="title">
                {{item.listid}}
                機種：{{ item.model }}
                <a-divider type="vertical" />
                階段：{{ item.build }}
                <a-divider type="vertical" />
                no：{{ item.sno }}
                <a-divider type="vertical" />
                站位名：{{ item.sname }}
                <a-divider type="vertical" />
                QPL：{{ item.qpl }}
                <a-divider type="vertical" />
                Process：{{ item.process }}
                <a-divider type="vertical" />
                部門：{{ item.dept }}
               </div>
            </a-list-item-meta>
          </a-list-item>
        </a-list>
      </div>
    </a-card>
  </div>
</template>
<script>

import {list,dely} from "@/api/fix/listy";
import {save,del} from "@/api/fix/list";
import security from '@/utils/security'
export default {
  data() {
    return {
      data: [],
      formData: {
        model: localStorage.getItem("model"),
      },
      role: security.getUser(),
    };
  },
  methods: {
    findSubmit(){
      list(this.formData).then((res) => {
        this.data = res.data;
      });
    },
    sub(data){
      if(data.status=='2'){
        var listyid=data.id;
        data.id=data.listid;
        save(data).then((res) => {
            if (res.code == 200&&data.id===undefined) {
            this.$message.success("添加成功");
            }
            else{
            this.$message.success("修改成功");
            }
        });
        dely(listyid).then((res) => {
          this.findSubmit()
        })
      }
      else{
        del(data.listid).then((res) => {
          this.findSubmit()
        })
        this.$message.success("刪除成功");
        dely(data.id).then((res) => {
          this.findSubmit()
        })
      }
    },
    delhcy(id){
      dely(id).then((res) => {
        this.findSubmit()
      })
    }

  },
  created: function () {
    list(this.formData).then((res) => {
      this.data = res.data;
    });
  },
};
</script>