import request from '@/utils/request'

// 分页查询Fixtrue list预修改信息
export function pageList(query) {
  return request({
    url: '/fixListy/pageList',
    method: 'get',
    params: query,
  })
}

// 不分页查询Fixtrue list预修改信息
export function list(data) {
  return request({
    url: '/fixListy/alllist',
    method: 'post',
    data: data
  })
}

// 添加Fixtrue list预修改信息
export function insertfy(data) {
  return request({
    url: '/fixListy/insert',
    method: 'post',
    data: data
  })
}

// 修改Fixtrue list预修改信息
export function update(data) {
  return request({
    url: '/fixListy/update',
    method: 'put',
    data: data,
  })
}

// 添加或修改Fixtrue list预修改信息
export function save(data) {
  return request({
    url: '/fixListy/save',
    method: 'post',
    data: data
  })
}

// 删除Fixtrue list预修改信息
export function dely(id) {
  return request({
    url: '/fixListy/'+id,
    method: 'delete'
  })
}

// 根据id查询Fixtrue list预修改信息
export function onebyid(id) {
  return request({
    url: '/fixListy/'+id,
    method: 'get'
  })
}
