import request from '@/utils/request'

// 分页查询Fixtrue list信息
export function pageList(query) {
  return request({
    url: '/fixList/allpageList',
    method: 'get',
    params: query,
  })
}

// 不分页查询Fixtrue list信息
export function list(data) {
  return request({
    url: '/fixList/list',
    method: 'post',
    data: data
  })
}

// 添加Fixtrue list信息
export function insert(data) {
  return request({
    url: '/fixList/insert',
    method: 'post',
    data: data
  })
}

// 修改Fixtrue list信息
export function update(data) {
  return request({
    url: '/fixList/update',
    method: 'put',
    data: data,
  })
}

// 添加或修改Fixtrue list信息
export function save(data) {
  return request({
    url: '/fixList/save',
    method: 'post',
    data: data
  })
}

// 删除Fixtrue list信息
export function del(id) {
  return request({
    url: '/fixList/'+id,
    method: 'delete'
  })
}

// 根据id查询Fixtrue list信息
export function onebyid(id) {
  return request({
    url: '/fixList/'+id,
    method: 'get'
  })
}
